import React, { Fragment } from 'react'

import Head from '../components/Head';
import PageTitle from '../components/PageTitle';
import ArticleTitle from '../components/ArticleTitle';
import ImgBox from '../components/ImgBox';

export default function Inherit() {
    return (
        <Fragment>
            <div id="p2">
                <Head />
                <PageTitle titl1="我们有" titl2="传承" imgsrc={require('../asset/img/dress_figure.png')} />
                <div className='m-body'>
                    <ArticleTitle value='苗疆服饰与蜡染' orderNum='壹' />
                    <div className='articlecontent'>
                        <p>在中国南方民族中,<b>「苗族服饰」</b>最为丰富多彩。贵州是苗族服饰最为精美之地其刺绣、蜡染、纺织、银饰都极为出色，充分体现出富有特色的苗族服饰艺术。他们不仅将文化传统倾注于口头文学，更将它倾注于服饰图案之中。苗族人将服饰图案作为史书，深切地表达历史并作为苗族群体的标志世代相传</p>
                        <ImgBox width={'95%'} height={'135%'} imgsrc='#' boxMargin='1rem auto' />
                        <p><b>「蜡染」</b>，古称蜡缬，是用蜡刀蘸熔蜡绘花于布后以蓝靛浸染，既染去蜡，布面就呈现出蓝底白花或白底蓝花的多种图案，同时，在浸染中，作为防染剂的蜡自然龟裂，使布面呈现特殊的“冰纹”，尤具魅力</p>
                        <ImgBox width={'95%'} height={'135%'} imgsrc='#' boxMargin='1rem auto' />
                    </div>
                    <ArticleTitle value='蜡染养蜂皆非遗' orderNum='贰' />
                    <div className='articlecontent'>
                        <ImgBox width={'95%'} height={'135%'} imgsrc='#' boxMargin='1rem auto' />
                    </div>
                    <ArticleTitle value='且兰古国' orderNum='叁' />
                    <div className='articlecontent'>
                        <p>且兰国建立于<b>先秦时期</b>据《华阳国志》载：“周之季世，楚顷襄王遣将军庄蹻溯沅水，出且兰，以伐夜郎，植牂柯系舡于且兰。既克夜郎，秦夺楚黔中地，无路得归，遂留王之，号为庄王。以且兰有椓舡牂柯处，乃改其名为牂柯。” 战国末期，秦国平定黔中郡，后秦朝开通五尺道，派遣官吏管理西南夷。秦朝灭亡后，且兰断绝与汉朝的来往。后来汉代再度开拓西南夷，且兰归附汉朝，其君主被任命为且兰君汉武帝元鼎五年（前112），南越丞相吕嘉反叛汉朝，汉武帝派驰义侯发西南夷兵征讨南越。且兰君担心远征造成国中空虚，周边国家会乘机来袭，于是背叛汉朝，杀死汉朝使者和犍为郡太守。元鼎六年（前111），汉朝发兵征讨西南夷，置牂牁郡，以且兰国置故且兰县魏晋时期，故且兰县更名为且兰县，牂牁郡治所迁于万寿县。南北朝时期，且兰县废</p>
                        <ImgBox width={'95%'} height={'45%'} imgsrc='#' boxMargin='1rem auto' />
                        <ImgBox width={'95%'} height={'45%'} imgsrc='#' boxMargin='1rem auto' />
                        <ImgBox width={'95%'} height={'45%'} imgsrc='#' boxMargin='1rem auto' />
                    </div>
                </div>
            </div>
        </Fragment>
    )
}
