import React, {Fragment} from 'react'

import Comb from '../components/Comb'
import IntroSec from '../components/IntroSec'
import { ArrowDownB } from '../components/Arrow'

export default function Hmong() {
    const intro1 = [
        {id: '01', titl: '⌈ 情节 ⌋', contnt: '张晓慧是一名出身在黔东南州朱家山自然保护区、朱家山国家森林公园内的上塘镇永爱村苗族姑娘，骨子里就拥有着农村情节'},
        {id: '02', titl: '⌈ 经历 ⌋', contnt: '张晓慧多年行医，造就了她慈爱、关注弱小与坚韧不拔的毅力'},
        {id: '03', titl: '⌈ 爱好 ⌋', contnt: '张晓慧是苗疆有名的养蜂人，她不仅传承着古法养蜂的习俗和技艺。还精通传统养蜂的诱蜂时间、诱蜂地点、诱蜂方法和管理技术，并多次获得非遗传承研培的资格。同时她也认真学习刺绣、蜡染技术，保护非遗、传承非遗'}
        ]
    const intro2 = [
        {id: '04', titl: '⌈ 信念 ⌋', contnt: '2018年，张晓慧成立了贵州苗疆蜂业科技发展有限公司，免费向村民提供技术培训和发放蜂种，在不到两年的时间里，她在创业这条路上开坏了8个车轮。带领企业始终坚持<b>“采集甜蜜，回馈故土，青山绿水，共同致富”</b>的目标'},
        {id: '05', titl: '⌈ 关爱 ⌋', contnt: '公司在员工录用方面坚持<b>优先录用妇女同胞、应届毕业生、当地贫困农民、残疾人和军人</b>，为这些人群就业提供保障'},
        {id: '06', titl: '⌈ 爱心 ⌋', contnt: '疫情间，听闻黄平县防护物质短缺，为了让坚守在一线的工作人员能够得到有效防护，驱车40多公里来到黄平县卫健局、黄平县法院<b>捐赠了5000余个口罩</b>，另组织公司员工为蜂场周边各村老百姓<b>免费发放口罩共计5000余个</b><br/>黄平县人民医院的医务人员驰援武汉之前，作为曾经在医院工作过的她，对曾经的同行业有着特殊的感情，她到农户家购买了一头约300斤的土猪捐赠给黄平县人民医院，表达对曾经同行的特殊敬意'},
        {id: '07', titl: '⌈ 荣誉 ⌋', contnt: '2019年，贵州省妇女联合会授予张晓慧<b>“贵州省三八红旗手荣誉称号”</b>，以及<b>州劳动模范称号</b>'},
        {id: '08', titl: '⌈ 展望 ⌋', contnt: '依托贵州大学动物科学院、贵州大学昆虫研究所、湖南农业大学专家学科技术资源优势成立一个<b>“苗疆蜂业中蜂保种育种研究中心”</b><br/>正在建设<b>贵州省首个蜜粉源植物种质资源库</b>，以此作为贵州省蜜粉源植物就地、迁地保育体系的主要基地，按照蜜蜂生态养殖的功能要求、农业结构调整和林下经济新业态发展需要，建立一个集“蜜粉源植物博览、种质保存、科普科研、蜜粉源植物良种选繁、产业示范、教学实习、采摘体验、观光旅游、森林康养”于一体的<b>多功能产业示范基地和乡村振兴建设示范基地</b>'}
    ]
    const imgsrcs = {
        imgsrc: [
            [{ id: '01', titl: '01', src: 'https://n.sinaimg.cn/spider20230112/100/w1500h1000/20230112/53b3-d3407bbb32a89619d85b555133b04858.jpg' },
            { id: '02', titl: '02', src: 'https://img0.baidu.com/it/u=3156137851,1307209439&fm=253&fmt=auto&app=138&f=JPEG?w=889&h=500' },
            { id: '03', titl: '03', src: 'https://5b0988e595225.cdn.sohucs.com/images/20181001/8db0176342cb46afbcd02e19b07476ed.jpeg' },
            { id: '04', titl: '04', src: 'https://img0.baidu.com/it/u=3156137851,1307209439&fm=253&fmt=auto&app=138&f=JPEG?w=889&h=500' }], 
            [{ id: '05', titl: '05', src: 'https://5b0988e595225.cdn.sohucs.com/images/20181001/8db0176342cb46afbcd02e19b07476ed.jpeg' },
            { id: '06', titl: '06', src: 'https://n.sinaimg.cn/spider20230112/100/w1500h1000/20230112/53b3-d3407bbb32a89619d85b555133b04858.jpg' },
            { id: '07', titl: '07', src: `https://img0.baidu.com/it/u=3156137851,1307209439&fm=253&fmt=auto&app=138&f=JPEG?w=889&h=500` }], 
            [{ id: '08', titl: '08', src: `https://5b0988e595225.cdn.sohucs.com/images/20181001/8db0176342cb46afbcd02e19b07476ed.jpeg` },
            { id: '09', titl: '09', src: `https://img0.baidu.com/it/u=3156137851,1307209439&fm=253&fmt=auto&app=138&f=JPEG?w=889&h=500` },
            { id: '10', titl: '10', src: `https://n.sinaimg.cn/spider20230112/100/w1500h1000/20230112/53b3-d3407bbb32a89619d85b555133b04858.jpg` },
            { id: '11', titl: '11', src: `https://5b0988e595225.cdn.sohucs.com/images/20181001/8db0176342cb46afbcd02e19b07476ed.jpeg` }]
        ]
    }
  return (
    <Fragment>
        <div className="nav">
            <h2 className="title">苗疆蜂业</h2>
        </div>
        <div className="pagetitle">
            <div className="word">
                <div className="pt"><h3>我是</h3><h1>苗族人</h1></div>
                <div className="pt"><h3>我是</h3><h1>张晓慧</h1></div>
            </div>
            <div className="icon-box">
                <ArrowDownB />
            </div>
        </div>
        <div className="content">
            <div className="identity">
                <p>贵州苗疆蜂业科技有限发展公司董事长</p>
                <p>“苗山花”品牌创始人</p>
                <p>中国苗专委发展部常务副部长</p>
                <p>贵州省女企业家商会理事</p>
            </div>
            <div className="imgbox">
                <img src="" alt="" />
            </div>
              {
                  intro1.map((intro, index) => (
                      <IntroSec key={intro.id} {...intro}/>
                  ))
              }
            <div className="imgbox">
                <span><img src="" alt="" /></span>
            </div>
              {
                  intro2.map((intro, index) => (
                    <IntroSec key={intro.id} {...intro}/>
                  ))
              }
            <div className="bussiness">
                <div className="my-bus">
                    <h3>我的</h3>
                    <h2>甜蜜事业</h2>
                </div>
                <Comb {...imgsrcs}/>
                <div className="navig">
                    <span> {'<'} </span>
                    <span> {'>'} </span>
                </div>
            </div>
        </div>
    </Fragment>
  )
}
