import React,{ useRef, useEffect, useState } from 'react'
import { nanoid } from 'nanoid'
import img from '../asset/img/soup1.png'

export default function Expert(props) {

  const expertRef = useRef();
  const [height, setHeight] = useState(0);

  const datas = [
    {id: nanoid(), expertImg: img, expertName: '专家1', expertProfile: '专家简介'},
    {id: nanoid(), expertImg: '#', expertName: '专家2', expertProfile: '专家简介'},
    {id: nanoid(), expertImg: '#', expertName: '专家3', expertProfile: '专家简介'},
    {id: nanoid(), expertImg: '#', expertName: '专家4', expertProfile: '专家简介'},
    {id: nanoid(), expertImg: '#', expertName: '专家5', expertProfile: '专家简介'}
  ]
  useEffect(()=>{
    setHeight(expertRef.current.scrollHeight * 4);
  }, [])
  return (
    <ul className='expertlist' style={{maxHeight: height}}>
      {
        datas.map((data, index) => (
          <li key={data.id} ref={expertRef}>
            <div className="expertHead">
              <span><img src={data.expertImg} alt="" /></span>
            </div>
            <div className="expertInfo">
              <h3>{data.expertName}</h3>
              <p>{data.expertProfile}</p>
            </div>
          </li>
        ))
      }
    </ul>
  )
}
