import React, { Fragment } from 'react'
import { nanoid } from 'nanoid';
import { NavLink, Outlet } from 'react-router-dom'

import { ArrowDownB } from '../components/Arrow'
import ImgBox from '../components/ImgBox';
import ArticleTitle from '../components/ArticleTitle';
import MapContainer from '../components/MapContainer';

import aboutBackground from '../asset/img/about.jpeg'
import titlIcon1 from '../asset/img/titlIcon1.png'
import titlIcon2 from '../asset/img/titlIcon2.png'

export default function About() {

    const aboutinfo = '「贵州苗疆蜂业科技有限公司」<br />成立于2018年5月<br />总部位于贵州省黄平县上塘镇永爱村<br />公司注册资本金1000万元<br />截至2022年8月30日，公司总资产3100万元<br /><br />是一家集<br />蜜蜂养殖<br />蜂种繁育<br />供应蜂蜜<br />销售蜂产品研发<br />养蜂设施供应<br />养蜂技术培训及咨询服务<br />蜜源植物种植<br />蜂蜜保健品开发<br />蜂疗技术研发及应用<br />蜂蜡加工等<br />养蜂产业化发展的一家科技型实体企业'
    const supports = [
        {id: nanoid(), img: '#', time: '2021.11.3', profile: '校企合作 凯里学院人文学院与贵州苗疆蜂业签约挂牌仪式'},
        {id: nanoid(), img: '#', time: '2020.8.2', profile: '苗疆蜂业招蜂引蝶，职等你来！！！'},
        {id: nanoid(), img: '#', time: '2020.2.5', profile: '齐心协力，共同抗“疫”'},
        {id: nanoid(), img: '#', time: '2020.1.14', profile: '“话党恩，谈幸福”老人新春茶话会'},
    ]

    return (
        <Fragment>
            <div className="nav">
                <h2 className="title">苗疆蜂业</h2>
            </div>
            <div className="pagetitle" id='about'>
                <ImgBox imgsrc={aboutBackground} className='pos-ab'/>
                <div className="imgdesc">
                    <h1>苗疆蜂业</h1>
                </div>
                <div className="icon-box" >
                    <ArrowDownB color='#fff'/>
                </div>
            </div>
            <div className="content" id='aboutContent'>
                <div className="identity">
                    <p dangerouslySetInnerHTML={{__html: aboutinfo}} />
                </div>
                <section>
                    <div className="secTitl">
                        <div className="titlIcon">
                            <span><img src={titlIcon1} alt="" /></span>
                        </div>
                        <ArticleTitle value='五谷“蜂”登' style={{fontSize:'1.2rem', marginTop: 0, marginBottom: '1.2rem'}}/>
                        <ul className='gulist'>
                            <li><b>产业之谷 — </b>中华蜂全产业链发展</li>
                            <li><b>浪漫之谷 — </b>以爱为主题的发展乐土</li>
                            <li><b>幸福之谷 — </b>高幸福指数目的地</li>
                            <li><b>振兴之谷 — </b>乡村振兴典范</li>
                            <li><b>发展之谷 — </b>具备带动乡村发展的可持续发展道路</li>
                        </ul>
                    </div>
                </section>
                <section>
                    <div className="secTitl">
                        <div className="titlIcon">
                            <span><img src={titlIcon2} alt="" /></span>
                        </div>
                        <ArticleTitle value='三区一基地' style={{fontSize:'1.2rem', marginTop: 0, marginBottom: '1.2rem'}}/>
                        <div className="Mapcontainer">
                             <MapContainer />
                        </div>
                    </div>
                </section>
                <section style={{marginTop: '2rem'}}>
                    <div className="sechead">
                        <NavLink to='expert' className={({isActive})=> isActive ? 'active' : ''}>专家成员</NavLink>
                        <NavLink to='patent' className={({isActive})=> isActive ? 'active' : ''}>专利技术</NavLink>
                        <NavLink to='university' className={({isActive})=> isActive ? 'active' : ''}>高校支持</NavLink>
                    </div>
                    <div className="secbody">
                        <Outlet />
                    </div>
                </section>
                <section>
                    <div className="secTitl">
                        <ArticleTitle value='政府支持' style={{fontSize:'1.2rem', marginTop: 0, marginBottom: '1.2rem'}}/>
                        <ul className='suptlist'>
                            {
                                supports.map((item, index) => (
                                    <li key={item.id}>
                                        <div className="suptimg">
                                            <span><img src={item.img} alt="" /></span>
                                        </div>
                                        <div className="suptinner">
                                            <h4><b>{item.time}</b></h4>
                                            <p>{item.profile}</p>
                                        </div>
                                    </li>
                                ))
                            }
                        </ul>
                    </div>
                </section>
            </div>
        </Fragment>
    )
}
